@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
body{
  background-color: #232427;
  color: white;
  font-family: 'Poppins',sans-serif;
  margin: 0;
  
}
.root{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  
}
.movie{
  background-color: #232427;
  position: relative;
  margin: 1rem;
  width: 320px;
  
  border-radius: 15px;
  box-shadow: inset 6px 6px 6px rgba(255,255,255,0.2),
              inset -6px -6px 15px rgba(255,255,255,0.1),
              6px 6px 16px rgba(0,0,0,0.3),
              -6px -6px 16px rgba(0,0,0,0.1);
  overflow: hidden;
  position: relative;
}

.movie img{
  max-width: 100%;

}
.movie-container{
 
  display: -webkit-flex;
 
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

}
.movie-info{
  display: -webkit-flex;
  display: flex;
  padding: 1rem;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;

}
.movie-info h3{
  margin: 0;
}
.movie-over{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #2e2e33;
  color: #fff;
  padding: 1rem;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  overflow: auto;
  max-height: 100%;



}
header{
  background-color: #2e2e33;
  padding: 0.2rem;
  display: -webkit-flex;
  display: flex;
  

}
.search{
  font-family: inherit;
  font-size: 1.2rem;
  border: 2px solid #232427;
  border-radius: 50px;
  background-color: transparent;
  color: white;
  padding: 0.5rem 1.5rem;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  left: 950px;
  top: 20px;
}
.search:focus{
  outline: 0;
  
}
.movie:hover .movie-over{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.tag{
 background-color: #22254b;
 padding: 0.5rem;
 border-radius: 5px;

}
.tag.green{
  color: green;
}
.tag.orange{
  color: orange;
}
.tag.red{
  color: red;
}
::-webkit-scrollbar{
  width: 16px;
  height: 10px;
}
::-webkit-scrollbar-thumb{
  background: linear-gradient(transparent,#30ff00);
  border-radius: 6px;

}
::-webkit-scrollbar-thumb:hover{
  background: linear-gradient(transparent,#00c6ff);
}
h2{
  color: #0e3742;
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;

  
}
@-webkit-keyframes animate{
  0%,18%,20%,50.1%,60%,65.1%,80%,90.1%,92%{
    color: #0e3742;
    text-shadow: none;
  }
  18.1%,20.1%,50%,60.1%,65%,80.1%,85%,89%,92.1%,100%{
    color: #fff;
    text-shadow: 0 0  10px #03bcf4,
    0 0 20px #03bcf4,
    0 0 40px #03bcf4,
    0 0 80px #03bcf4,
    0 0 160px #03bcf4;

  }
}
@keyframes animate{
  0%,18%,20%,50.1%,60%,65.1%,80%,90.1%,92%{
    color: #0e3742;
    text-shadow: none;
  }
  18.1%,20.1%,50%,60.1%,65%,80.1%,85%,89%,92.1%,100%{
    color: #fff;
    text-shadow: 0 0  10px #03bcf4,
    0 0 20px #03bcf4,
    0 0 40px #03bcf4,
    0 0 80px #03bcf4,
    0 0 160px #03bcf4;

  }
}
footer{
  position: relative;
  display: -webkit-flex;
  display: flex;
  background-color: #2e2e33;
}
footer h3{

 position: absolute;
 display: -webkit-flex;
 display: flex;
  
  left: 40%;
}
h3{
  text-shadow: 0 0 10px #00b3ff,
               0 0 20px #00b3ff,
               0 0 40px #00b3ff,
               0 0 80px #00b3ff,
               0 0 120px #00b3ff;
  -webkit-animation: animateh3 5s linear infinite;
          animation: animateh3 5s linear infinite;
}
@-webkit-keyframes animateh3 {
  0%{
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100%{
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}
@keyframes animateh3 {
  0%{
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100%{
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}
